"use client";

import React from "react";
import { useTheme } from "next-themes"; // Importamos para detectar el tema actual
import { useRouter } from "next/navigation";

interface ModuleCardProps {
  title: string;
  status: "completed" | "active" | "locked";
  id: number;
}

const ModuleCard: React.FC<ModuleCardProps> = ({ title, status, id }) => {
  const router = useRouter();
  const { theme } = useTheme(); // Detectamos el tema actual (light o dark)

  const getStatusStyles = () => {
    switch (status) {
      case "completed":
        return "bg-gray-100 text-primary border border-primary";
      case "active":
        return "bg-primary text-white";
      case "locked":
        return "bg-gray-100 text-gray-500 border border-gray-300";
      default:
        return "";
    }
  };

  const getIcon = () => {
    const basePath = theme === "dark" ? "/assets/dark" : "/assets/light";
    switch (status) {
      case "completed":
        return `${basePath}/check.svg`;
      case "active":
        return `${basePath}/arrow-right.svg`;
      case "locked":
        return `${basePath}/lock.svg`;
      default:
        return "";
    }
  };

  return (
    <div
      className={`flex items-center justify-between p-4 rounded-md cursor-pointer ${getStatusStyles()}`}
      onClick={() => status === "active" && router.push(`/dashboard/progress/module/${id}`)}
    >
      <span className="font-medium">{title}</span>
      <img src={getIcon()} alt={status} className="w-5 h-5" />
    </div>
  );
};

export default ModuleCard;
